.App {
  text-align: center;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.home {
  background-position:'center';
  background-attachment:'fixed';
  background-repeat:'no-repeat';
  background-size:'auto';
  background-image: url("https://wallpaperaccess.com/full/5651990.jpg") ;
  height: 800px;
}
.developer{
  height: 100%;
  width: 100%;
}
.sub_dev{
  position: relative;
  top: 40%;
  text-align: left;
  margin-left: 200px;
}
.sub_dev>h3{
  font-size: 30px;
  /* font-weight: bold; */
  color: white;
  text-shadow: #61dafb;
}
.sub_dev>h1{
  font-size: 55px;
  font-weight: bold;
  color: white;
}
.sub_dev>button{
  background-color: teal;
  width: 150px;
  padding: 8px;
  border-radius: 8px;
  font-weight:bold;
  margin-top: 20px;
  border: 1px solid white;
  color: white;
  
}
.sub_dev>button:hover{
 background-color: white;
 color: black;
 border: teal;
}



/* ===================================================================================== */
@media (max-width: 870px) {
  .sub_dev{
    margin-left: 100px;
  }
  .sub_dev>h3{
    font-size: 25px;
  }
  .sub_dev>h1{
    font-size: 35px;
  }
  .sub_dev>button{
    width: 130px;
    padding: 6;
  }
}
@media (max-width: 560px) {
  .sub_dev{
    margin-left: 15px;
  }
  .sub_dev>h3{
    font-size: 15px;
  }
  .sub_dev>h1{
    font-size: 20px;
  }
  .sub_dev>button{
    width: 100px;
    padding: 3;
    font-size: 13px;
  }
}