        .navbar{
            position: fixed;
            color: white;
            font-family: "Ubuntu", sans-serif;
            transition: all 0.3s ease;
            width:100%;
            padding: 15px;
            padding-left: 100px;
            padding-right: 100px;
            display: flex;
            justify-content: space-between;
        }

        ._navbar {
            position: fixed;
            color: white;
            background: #0B1727;
            font-family: "Ubuntu", sans-serif;
            transition:  0.3s ease-out;
            width:100%;
            padding: 15px;
            padding-left: 100px;
            padding-right: 100px;
            display: flex;
            justify-content: space-between;
            z-index: 999;
          }
        
        
        .nav-links a {
        
        color: #fff;
        
        }
        
        /* LOGO */
        
        .logo {
        
        font-size: 32px;
        
        }
        
        /* NAVBAR MENU */
        
        .menu,._menu {
        
        display: flex;
        
        gap: 1em;
        
        font-size: 18px;
        
        }
        
        .menu li:hover,._menu li:hover {
        
        /* background-color: #4c9e9e; */
        
        /* border-radius: 5px; */
        color: red;
        transition: 0.3s ease;
        
        }
        
        .menu li,._menu li {
        
        padding: 5px 14px;
        
        }
        
        /* DROPDOWN MENU */
        
        .services {
        
        position: relative;
        
        }
        
