        /* ================================= */
        input[type=checkbox]{

            display: none;
            
            }
            
            /*HAMBURGER MENU*/
            
            .hamburger {
            
            display: none;
            
            font-size: 24px;
            
            user-select: none;
            
            }
            
            /* APPLYING MEDIA QUERIES */
        
            @media (max-width: 1000px) {
            
            .menu {
            
            display:none;
            
            position: absolute;
            
            background-color:none;
            
            right: 0;
            
            left: 0;
            
            text-align: center;
            
            padding: 16px 0;
            
            }
            ._menu {
            
            display:none;
            margin-top: 15px;
            position: absolute;
            transition: 0.3s ease;
            background-color:#0B1727;
            
            right: 0;
            
            left: 0;
            
            text-align: center;
            
            padding: 16px 0;
            
            }
            
            .menu li:hover,._menu li:hover  {
            
            display: inline-block;
            
            background-color:none;
            
            transition: 0.3s ease;
            
            }
            ._menu li:hover  {
            
            display: inline-block;
            
            background-color:#0B1727;
            
            transition: 0.3s ease;
            
            }
           
            
            .menu li + li ,._menu li + li {
            
            margin-top: 12px;
            
            }
           
            
            input[type=checkbox]:checked ~ .menu,input[type=checkbox]:checked ~ ._menu{
            
            display: block;
            
            }
            
            
            .hamburger {
            
            display: block;
            
            }
            
            .dropdown {
            
            left: 50%;
            
            top: 30px;
            
            transform: translateX(35%);
            
            }
            
            .dropdown li:hover {
            
            background-color: #0B1727;
            
            }
            
            }